@use 'variables' as var; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: var.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: var.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: var.$desktop) {
        @content;
    }
}