html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  background-color: #000000;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

p {
  font-size: 2rem;
  color: #000000;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

a, p, h1, h2, h3 {
  color: #FFFFFF;
}

/** Media Queries **/
.header {
  background-color: #000000;
  padding: 2rem;
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #FFFFFF;
}
@media (min-width: 992px) {
  .header .contenido-header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .contenido-header h1 {
  text-align: center;
}

.nav_principal {
  margin-top: 2rem;
  display: flex;
  gap: 2rem;
}
@media (min-width: 992px) {
  .nav_principal {
    margin: 0;
  }
}
.nav_principal a {
  color: #FFFFFF;
  font-size: 2rem;
}
.nav_principal a:hover {
  color: #FEF46D;
  transition: 300ms ease-in-out;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.116);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 4rem;
  text-align: center;
  text-shadow: 1px 1px 2px black;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 7rem;
    text-align: left;
  }
}
.contenido-video h2::selection {
  background-color: #000000;
}
.contenido-video p {
  padding: 1rem;
  font-size: 1.6rem;
  background-color: #F53756;
  color: #FFFFFF;
  display: block;
  text-align: center;
  max-width: 30rem;
  margin: 0 auto;
  font-weight: 700;
}
@media (min-width: 768px) {
  .contenido-video p {
    display: inline;
    font-size: 2rem;
  }
}
.contenido-video p::selection {
  color: #000000;
}

.sobre-festival {
  padding: 5rem 0;
  display: grid;
  gap: 4rem;
  align-items: center;
}
@media (min-width: 768px) {
  .sobre-festival {
    grid-template-columns: repeat(2, 1fr);
  }
}

.contenido-festival .fecha {
  background-color: #F53756;
  padding: 1rem;
  display: inline-block;
  font-weight: 700;
  margin: 1rem 0;
}
.contenido-festival p {
  line-height: 1.6;
}

.imagen {
  position: relative;
  cursor: pointer;
}

.texto-imagen {
  line-height: 1.6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.1) 0%, #000000 70%);
  opacity: 0;
  transition: all 500ms ease-in-out;
  text-align: center;
}
.texto-imagen:hover {
  opacity: 1;
}

.fechas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .fechas {
    flex-direction: row;
  }
}
.fechas .fecha {
  text-align: center;
  padding: 0 4rem;
  line-height: 1.6;
  margin-bottom: 4rem;
}
.fechas .fecha:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .fechas .fecha {
    margin: 0;
  }
  .fechas .fecha:nth-child(2) {
    border-left: 2px solid #575757;
  }
}